header{
    order: 1;
    max-width: 100%;
    margin-top: 80px;
    .header_top{
        box-shadow: 0px 5px 20px 0px rgba(0,0,0,0.75);
        top: 0;
        left: 0;
        width: 100%;
        background: rgb(255, 255, 255);
        z-index: 9999;
        position: fixed;
        .wrap{ 
            .row{
                box-sizing: border-box;
                background: rgb(255, 255, 255);
                .social_icons{
                    width: 100%;
                    justify-content: flex-start;
                    a{
                        margin: 0 20px;
                        img{
                            width: 50px;
                        }
                        &:last-child{
                            margin-right: 0;
                        }
                        &:first-child{
                            margin-left: 0;
                        }
                    }
                }
                .logo{
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    top: 12px;
                    position: relative;
                    z-index: 2;
                    img{
                        width: 150px;
                    }
                }
                .information{
                    width: 100%;
                    text-align: right;
                    font-weight: 600;
                    font-size: 20px;
                    a{
                        color: #000;
                        font-weight: 600;
                        font-size: 25px;
                    }
                    p{
                        line-height: 1.5;
                        font-weight: 600;
                        &:nth-child(1){
                            color: rgb(116, 179, 0);   
                            font-weight: 600;
                            line-height: 1.2;
                            font-size: 18px;
                        }
                        &:nth-child(3){
                            line-height: 1.2;
                            font-size: 18px;
                        }
                    }

                }
                >div:last-child{
                    z-index: -9;
                    width: 100%;
                    // background: url(../img/circle.png) 50% 50% / contain no-repeat;
                    box-shadow: 0px 5px 20px 0px rgba(0,0,0,0.75);
                    height: 150px;
                    border-radius: 50%;
                    width: 200px;
                    background: #fff;
                    position: absolute;
                    bottom: -40px;
                    left: 50%;
                    transform: translateX(-50%);
                    z-index: -4;
                }
            }
        }
    }
    .header_banner{
        color: rgb(255, 255, 255);
        width: 100%;
        height: 650px;
        background: url(../img/header.jpg) 50% 50% / cover no-repeat, rgba(0, 0, 0, 1);
        display: flex;
        flex-direction: column;
        position: relative;
        div:first-child{
            width: 100%;
            background: url(../img/circle_1.png) 50% 50% / contain no-repeat;
            height: 18px;
            position: absolute;
            bottom: 0;
            z-index: 8;
            left: 50%;
            transform: translateX(-50%);
            &::after{
                content: '';
                width: 40.5%;
                background: #f7f6f2;
                height: 18px;
                position: absolute;
                bottom: 0;
                left: 0;
            }
            &::before{
                content: '';
                width: 40.5%;
                background: #f7f6f2;
                height: 18px;
                position: absolute;
                bottom: 0;
                right: 0;
            }
        }
        &::after{
            content: '';
            background: rgba(0, 0, 0, .3);
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 0;
        }
        .wrap{
            z-index: 2;
            text-align: center;
            position: relative;
            img{
                width: 32px;
            }
            .leaf{
                position: absolute;
                width: auto;
                &:nth-of-type(2){
                    left: -120px;
                    top: 40px;
                    transform: rotate(45deg);
                }
                &:nth-of-type(3){
                    right:-8px;
                    bottom: -20px;  
                    transform: rotate(210deg);
                    width: 180px;
                }
                &:nth-of-type(4){
                    right: -100px;
                    top: -50px;
                }
            }
            .banner_info{
                width: 750px;
                background: rgba(0, 0, 0, 0.6);
                padding: 20px 10px;
                text-align: center;
                border-radius: 20px;
                color: rgb(255, 255, 255);
                margin: 35px 0 45px;
                h1{
                    font-size: 45px;
                    text-transform: uppercase;
                    margin-bottom: 10px;
                    font-weight: 400;
                    font-family: Arial, Helvetica, sans-serif;
                }
                h2{
                    font-weight: 200;
                    font-size: 25px;
                }
                .line{
                    width: 100px;
                    height: 2px;
                    background: rgb(255,255,255);
                    margin: 25px  auto;
                    border-radius: 10px;
                }
                p{
                    font-size: 20px;
                    margin-bottom: 25px;
                }
                span{
                    font-family: 'Circe';
                    font-size: 18px;
                }
                a{
                    color: rgb(255, 255, 255);
                    text-decoration: underline;
                }
            }
            .btn{
                margin: auto
            }
        }
    }
}

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px)
{
    header{
        .header_banner{
            .wrap {
                .leaf{
                    &:nth-of-type(4){
                        width: 120px;
                        top: 50px;
                        right: -50px;
                    }
                    &:nth-of-type(3){
                        width:  180px;
                        right: 20px;
                        bottom: 20px;                    
                    }
                    &:nth-of-type(2){
                        width:  180px;
                        left: -90px;
                        top: 50px;                  
                    }
                }
            }
        } 
    }
}


/* Medium Devices, Desktops */
@media only screen and (max-width : 992px)
{
    header{
        .header_top {
            .wrap {
                .row{
                    .logo{
                        left: 8px;
                        img{
                            width: 120px;
                        }
                    }
                    .information{
                        p{
                            font-size: 16px;
                        }
                    }
                } 
            }
        }
        .header_banner{
            div:first-child{
                background-size: cover;
                &::after, &::before{
                    display: none;
                }
            }
            .wrap {
                .banner_info{
                    width: 550px;
                }
                .leaf{
                    &:nth-of-type(4){
                        width: 140px;
                        top: -20px;
                        right: -40px;
                    }
                    &:nth-of-type(3){
                        width:  100px;
                        right: 40px;
                        bottom: 50px;                    
                    }
                    &:nth-of-type(2){
                        width:  180px;
                        left: -90px;
                        top: 40px;                
                    }
                }
            }
        } 
    }
}
@media only screen and (max-width : 800px)
{
    header{
        .header_top {
            .wrap {
                .row{
                    .logo{
                        left: 25px;
                    }
                } 
            }
        }
    }
}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px)
{
    header{
        margin-top: 60px;
        .header_top {
            .wrap {
                .row{
                    .social_icons{
                        display: none;
                    }
                    .logo{
                        left: 0;
                    }
                    .information{
                        display: none;
                    }
                } 
            }
        }
        .header_banner{
            height: 620px;
            &::after{
                background: rgba(0, 0, 0, 0.5)
            }
            div:first-child{
                display: none;
            }
            .wrap {
                padding: 0;
                margin: 25px auto;
                >img{
                    display: none;
                }
                .banner_info{
                    margin: auto;
                    background: none;
                    width: auto;
                    p{ 
                        display: none;
                    }
                    h1{
                        font-weight: 600;
                        font-family: 'Circe';
                        margin-top: 30px;   
                        font-size: 38px;
                    }
                    h2{
                        font-weight: 600;
                        font-size: 27px;
                    }
                    .mobile_vers{
                        display: block;
                        span{
                            font-weight: 600;
                        }
                    }
                   a{
                        font-size: 18px;
                        font-weight: 600;
                    }
                }
                .leaf{
                    display: none;
                }
                .btn{
                    display: none;
                }
                .mobile{
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    width: 200px;
                    background: #fff;
                    color: #0a0a0a;
                    font-weight: 600;
                    font-size: 18px;
                    margin: 25px auto 0;
                    padding: 5px 15px;
                    border-radius: 50px;
                    a{
                        font-size: 18px;
                        color: #0a0a0a;
                        font-weight: 600;    
                    }
                    p{
                        margin-right: 10px;
                        font-weight: 600;
                    }
                }
                .mouse_scroll{
                    display: block;
                    margin: 10px auto 0;
                    position: relative;
                    width: 50px;
                    height: 50px;
                    img{
                        width: 30px;
                        position: absolute;
                        top: 0px;
                        left: 50%;
                        margin-left: -15px;
                        animation: mouse  1.2s infinite ease-out;
                    }
                }
            }
        } 
    }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px)
{
    header{
        margin-top: 0;
        .header_top {
            display: none;
        }
        .header_banner{
            .wrap {
                padding: 0 2px;
                margin: 10px auto;
                .banner_info{
                    padding: 0 5px;
                    position: relative;
                    p{
                        display: none;
                    }
                    h2{
                        margin-top: 0;
                        padding: 0;
                        font-size: 20px;
                    }
                    p{
                        font-size: 18px;
                    }
                    .mobile_vers{
                        display: block;
                    }
                }
            }
        } 
    }
}

/* Custom, iPhone Retina */
@media only screen and (max-width : 368px)
{
    header{
        .header_banner{
            height: 560px;
            .wrap {
                .banner_info{
                    position: relative;
                    p{
                        display: none;
                    }
                    h1{
                        font-family: 'Circe';
                        font-weight: 600;
                        font-size: 38px;
                        line-height: 50px;
                        margin: 0;
                    }
                    h2{
                        font-size: 18px;
                    }
                }
            }
        } 
    }
}

/* Custom, iPhone Retina */
@media only screen and (max-width : 320px)
{

}


@keyframes mouse{
    0%{
        opacity: 1;
    }
    50%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}