*{
    margin: 0;
    padding: 0;
    list-style: none;
    line-height: 1.4;
    text-decoration: none;
    &:active,&:focus{
        outline: none;
        border: none;
    }
}

@green: rgb(129, 199, 0);
@dark_green: rgb(90, 138, 0);
.flex(@jusify:space-between, @align:center){
    display: flex;
    align-items: @align;
    justify-content: @jusify;
}
.trans(@time:.3s){
    transition: @time all ease-in-out;
}
.default_btn{
    display: block;
    width: 244px;
    text-align: center;
    font-family: 'Circe';                    
    background: @green;
    color: #fff;
    font-weight: 500;
    padding: 15px 0;
    font-size: 18px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    .trans();
    &:hover{
        background: @dark_green;
    }
}
.wrap{
    box-sizing: border-box;
    max-width: 1150px;
    margin: auto;
    padding: 0 10px;
}
.row{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.mobile_vers{
    display: none;
}
.span(@family:'Helvetica', @weight: 600){
    font-family: @family;
    font-weight: @weight;
}
h1{
    font-family: 'Circe';
    font-weight: 600;
}
h2,h3,h4{
    font-family: 'Circe';
    font-weight: normal;
}
p,a{
    font-family: 'Circe';
    font-weight: normal;
}
input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

#sign form input[type="number"] {
    -moz-appearance: textfield;
    -webkit-appearance: none;
}
