
@font-face {
    font-family: 'Helvetica';
    src: url('../fonts/Arial-Bold/Arial-Bold.eot');
    src: url('../fonts/Arial-Boldr/Arial-Bold.eot') format('embedded-opentype'),
        url('../fonts/Arial-Bold/Arial-Bold.woff') format('woff'),
        url('../fonts/Arial-Bold/Arial-Bold.ttf') format('truetype'),
        url('../fonts/Arial-Bold/Arial-Bold.svg') format('svg');
    font-weight: bold;
    font-style: normal;
}
