* {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 1.4;
  text-decoration: none;
}
*:active,
*:focus {
  outline: none;
  border: none;
}
.default_btn {
  display: block;
  width: 244px;
  text-align: center;
  font-family: 'Circe';
  background: #81c700;
  color: #fff;
  font-weight: 500;
  padding: 15px 0;
  font-size: 18px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.3s all ease-in-out;
}
.default_btn:hover {
  background: #5a8a00;
}
.wrap {
  box-sizing: border-box;
  max-width: 1150px;
  margin: auto;
  padding: 0 10px;
}
.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.mobile_vers {
  display: none;
}
h1 {
  font-family: 'Circe';
  font-weight: 600;
}
h2,
h3,
h4 {
  font-family: 'Circe';
  font-weight: normal;
}
p,
a {
  font-family: 'Circe';
  font-weight: normal;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
#sign form input[type="number"] {
  -moz-appearance: textfield;
  -webkit-appearance: none;
}
header {
  order: 1;
  max-width: 100%;
  margin-top: 80px;
}
header .header_top {
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.75);
  top: 0;
  left: 0;
  width: 100%;
  background: #ffffff;
  z-index: 9999;
  position: fixed;
}
header .header_top .wrap .row {
  box-sizing: border-box;
  background: #ffffff;
}
header .header_top .wrap .row .social_icons {
  width: 100%;
  justify-content: flex-start;
}
header .header_top .wrap .row .social_icons a {
  margin: 0 20px;
}
header .header_top .wrap .row .social_icons a img {
  width: 50px;
}
header .header_top .wrap .row .social_icons a:last-child {
  margin-right: 0;
}
header .header_top .wrap .row .social_icons a:first-child {
  margin-left: 0;
}
header .header_top .wrap .row .logo {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 12px;
  position: relative;
  z-index: 2;
}
header .header_top .wrap .row .logo img {
  width: 150px;
}
header .header_top .wrap .row .information {
  width: 100%;
  text-align: right;
  font-weight: 600;
  font-size: 20px;
}
header .header_top .wrap .row .information a {
  color: #000;
  font-weight: 600;
  font-size: 25px;
}
header .header_top .wrap .row .information p {
  line-height: 1.5;
  font-weight: 600;
}
header .header_top .wrap .row .information p:nth-child(1) {
  color: #74b300;
  font-weight: 600;
  line-height: 1.2;
  font-size: 18px;
}
header .header_top .wrap .row .information p:nth-child(3) {
  line-height: 1.2;
  font-size: 18px;
}
header .header_top .wrap .row > div:last-child {
  z-index: -9;
  width: 100%;
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.75);
  height: 150px;
  border-radius: 50%;
  width: 200px;
  background: #fff;
  position: absolute;
  bottom: -40px;
  left: 50%;
  transform: translateX(-50%);
  z-index: -4;
}
header .header_banner {
  color: #ffffff;
  width: 100%;
  height: 650px;
  background: url(../img/header.jpg) 50% 50% / cover no-repeat, #000000;
  display: flex;
  flex-direction: column;
  position: relative;
}
header .header_banner div:first-child {
  width: 100%;
  background: url(../img/circle_1.png) 50% 50% / contain no-repeat;
  height: 18px;
  position: absolute;
  bottom: 0;
  z-index: 8;
  left: 50%;
  transform: translateX(-50%);
}
header .header_banner div:first-child::after {
  content: '';
  width: 40.5%;
  background: #f7f6f2;
  height: 18px;
  position: absolute;
  bottom: 0;
  left: 0;
}
header .header_banner div:first-child::before {
  content: '';
  width: 40.5%;
  background: #f7f6f2;
  height: 18px;
  position: absolute;
  bottom: 0;
  right: 0;
}
header .header_banner::after {
  content: '';
  background: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}
header .header_banner .wrap {
  z-index: 2;
  text-align: center;
  position: relative;
}
header .header_banner .wrap img {
  width: 32px;
}
header .header_banner .wrap .leaf {
  position: absolute;
  width: auto;
}
header .header_banner .wrap .leaf:nth-of-type(2) {
  left: -120px;
  top: 40px;
  transform: rotate(45deg);
}
header .header_banner .wrap .leaf:nth-of-type(3) {
  right: -8px;
  bottom: -20px;
  transform: rotate(210deg);
  width: 180px;
}
header .header_banner .wrap .leaf:nth-of-type(4) {
  right: -100px;
  top: -50px;
}
header .header_banner .wrap .banner_info {
  width: 750px;
  background: rgba(0, 0, 0, 0.6);
  padding: 20px 10px;
  text-align: center;
  border-radius: 20px;
  color: #ffffff;
  margin: 35px 0 45px;
}
header .header_banner .wrap .banner_info h1 {
  font-size: 45px;
  text-transform: uppercase;
  margin-bottom: 10px;
  font-weight: 400;
  font-family: Arial, Helvetica, sans-serif;
}
header .header_banner .wrap .banner_info h2 {
  font-weight: 200;
  font-size: 25px;
}
header .header_banner .wrap .banner_info .line {
  width: 100px;
  height: 2px;
  background: #ffffff;
  margin: 25px  auto;
  border-radius: 10px;
}
header .header_banner .wrap .banner_info p {
  font-size: 20px;
  margin-bottom: 25px;
}
header .header_banner .wrap .banner_info span {
  font-family: 'Circe';
  font-size: 18px;
}
header .header_banner .wrap .banner_info a {
  color: #ffffff;
  text-decoration: underline;
}
header .header_banner .wrap .btn {
  margin: auto;
}
/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) {
  header .header_banner .wrap .leaf:nth-of-type(4) {
    width: 120px;
    top: 50px;
    right: -50px;
  }
  header .header_banner .wrap .leaf:nth-of-type(3) {
    width: 180px;
    right: 20px;
    bottom: 20px;
  }
  header .header_banner .wrap .leaf:nth-of-type(2) {
    width: 180px;
    left: -90px;
    top: 50px;
  }
}
/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  header .header_top .wrap .row .logo {
    left: 8px;
  }
  header .header_top .wrap .row .logo img {
    width: 120px;
  }
  header .header_top .wrap .row .information p {
    font-size: 16px;
  }
  header .header_banner div:first-child {
    background-size: cover;
  }
  header .header_banner div:first-child::after,
  header .header_banner div:first-child::before {
    display: none;
  }
  header .header_banner .wrap .banner_info {
    width: 550px;
  }
  header .header_banner .wrap .leaf:nth-of-type(4) {
    width: 140px;
    top: -20px;
    right: -40px;
  }
  header .header_banner .wrap .leaf:nth-of-type(3) {
    width: 100px;
    right: 40px;
    bottom: 50px;
  }
  header .header_banner .wrap .leaf:nth-of-type(2) {
    width: 180px;
    left: -90px;
    top: 40px;
  }
}
@media only screen and (max-width: 800px) {
  header .header_top .wrap .row .logo {
    left: 25px;
  }
}
/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  header {
    margin-top: 60px;
  }
  header .header_top .wrap .row .social_icons {
    display: none;
  }
  header .header_top .wrap .row .logo {
    left: 0;
  }
  header .header_top .wrap .row .information {
    display: none;
  }
  header .header_banner {
    height: 620px;
  }
  header .header_banner::after {
    background: rgba(0, 0, 0, 0.5);
  }
  header .header_banner div:first-child {
    display: none;
  }
  header .header_banner .wrap {
    padding: 0;
    margin: 25px auto;
  }
  header .header_banner .wrap > img {
    display: none;
  }
  header .header_banner .wrap .banner_info {
    margin: auto;
    background: none;
    width: auto;
  }
  header .header_banner .wrap .banner_info p {
    display: none;
  }
  header .header_banner .wrap .banner_info h1 {
    font-weight: 600;
    font-family: 'Circe';
    margin-top: 30px;
    font-size: 38px;
  }
  header .header_banner .wrap .banner_info h2 {
    font-weight: 600;
    font-size: 27px;
  }
  header .header_banner .wrap .banner_info .mobile_vers {
    display: block;
  }
  header .header_banner .wrap .banner_info .mobile_vers span {
    font-weight: 600;
  }
  header .header_banner .wrap .banner_info a {
    font-size: 18px;
    font-weight: 600;
  }
  header .header_banner .wrap .leaf {
    display: none;
  }
  header .header_banner .wrap .btn {
    display: none;
  }
  header .header_banner .wrap .mobile {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 200px;
    background: #fff;
    color: #0a0a0a;
    font-weight: 600;
    font-size: 18px;
    margin: 25px auto 0;
    padding: 5px 15px;
    border-radius: 50px;
  }
  header .header_banner .wrap .mobile a {
    font-size: 18px;
    color: #0a0a0a;
    font-weight: 600;
  }
  header .header_banner .wrap .mobile p {
    margin-right: 10px;
    font-weight: 600;
  }
  header .header_banner .wrap .mouse_scroll {
    display: block;
    margin: 10px auto 0;
    position: relative;
    width: 50px;
    height: 50px;
  }
  header .header_banner .wrap .mouse_scroll img {
    width: 30px;
    position: absolute;
    top: 0px;
    left: 50%;
    margin-left: -15px;
    animation: mouse 1.2s infinite ease-out;
  }
}
/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {
  header {
    margin-top: 0;
  }
  header .header_top {
    display: none;
  }
  header .header_banner .wrap {
    padding: 0 2px;
    margin: 10px auto;
  }
  header .header_banner .wrap .banner_info {
    padding: 0 5px;
    position: relative;
  }
  header .header_banner .wrap .banner_info p {
    display: none;
  }
  header .header_banner .wrap .banner_info h2 {
    margin-top: 0;
    padding: 0;
    font-size: 20px;
  }
  header .header_banner .wrap .banner_info p {
    font-size: 18px;
  }
  header .header_banner .wrap .banner_info .mobile_vers {
    display: block;
  }
}
/* Custom, iPhone Retina */
@media only screen and (max-width: 368px) {
  header .header_banner {
    height: 560px;
  }
  header .header_banner .wrap .banner_info {
    position: relative;
  }
  header .header_banner .wrap .banner_info p {
    display: none;
  }
  header .header_banner .wrap .banner_info h1 {
    font-family: 'Circe';
    font-weight: 600;
    font-size: 38px;
    line-height: 50px;
    margin: 0;
  }
  header .header_banner .wrap .banner_info h2 {
    font-size: 18px;
  }
}
/* Custom, iPhone Retina */
@keyframes mouse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.side_bar {
  display: none;
}
.contacts {
  display: none;
  position: fixed;
  z-index: 99999;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: url(../img/contact.jpg) 0% 35% / cover no-repeat;
}
.contacts .wrap {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}
.contacts .wrap .container {
  position: relative;
  background: #fff;
  padding: 20px 15px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.contacts .wrap .container .close {
  cursor: pointer;
  top: 15px;
  right: 15px;
  width: 25px;
  position: absolute;
}
.contacts .wrap .container .close img {
  width: 100%;
}
.contacts .wrap .container img {
  width: 150px;
}
.contacts .wrap .container p:nth-of-type(1) {
  font-size: 20px;
  margin: 25px 0 0;
}
.contacts .wrap .container p:nth-of-type(2) {
  font-size: 16px;
  margin: 20px 0 0;
}
.contacts .wrap .container a {
  color: #222;
  font-weight: 600;
