.side_bar{
    display: none;
}
.contacts{
    display:none;
    position: fixed;
    z-index: 99999;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background: url(../img/contact.jpg) 0% 35% / cover no-repeat;
    .wrap{
        display: flex;
        height: 100%;
        justify-content: center;
        align-items: center;
        .container{
            position: relative;
            background: #fff;
            padding: 20px 15px;
            border-radius: 15px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            .close{
                cursor: pointer;
                top: 15px;
                right: 15px;
                width: 25px;
                position: absolute;
                img{
                    width: 100%;
                }
            }
            img{
                width: 150px;
            }
            p{
                &:nth-of-type(1){
                    font-size: 20px;
                    margin: 25px 0 0;
                }            
                &:nth-of-type(2){
                    font-size: 16px;
                    margin: 20px 0 0;
                }            
            }
            a{
                color: #222;
                font-weight: 600;
                text-decoration: underline;
                font-size: 20px;
            }
        }
    }
}
.form{
    position: fixed;
    z-index: 9998;
    width: 100%;
    height: 100vh;
    background: #fff;
    top: 0;
    left: 0;
    display: none;
    .close{
        cursor: pointer;
        z-index: 9999999;
        width: 50px;
        position: absolute;
        right: 50px;
        top: 25px;
    }
}
.succes{
    background: rgba(0, 0, 0, .6);
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: 9999;
    left: 0;
    top: 0;
    display: none;
    .wrap{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        margin: auto;
        .box{
            padding: 15px 25px;
            border-radius: 15px;
            text-align: center;
            background: #fff;
            p{
                font-size: 22px;
            }
            img{
                margin-bottom: 25px;
                width: 80px;
            }
        }
    }
}
.description{
