.tovar{
    margin-bottom: 80px;
    .wrap{
        .title_tovar{
            .flex(center,center);
            text-align: center;
            .line{
                width: 180px;
                height: 2px;
                background: @green;
            }
            h3{
                font-size: 30px;
                margin: 0 40px;
                line-height: 1.4;
                font-family: Arial, Helvetica, sans-serif;
                span{
                    .span();
                }
            }
        }
        .tovar_list_01{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin: 25px 0;
            .tovar_box{
                width: 145px;
                padding: 20px 5px;
                border: 2px rgba(0, 0, 0, 0.2) solid;
                border-radius: 25px;
                margin: 10px 14px;
                cursor: pointer;
                .trans();
                &:hover{
                    box-shadow: 0 5px 10px rgba(0, 0, 0, .3);
                    border-color: @green;
                }   
                &:nth-of-type(1),&:nth-of-type(7){
                    margin-left: 0;
                }
                img{
                    width: 100px;
                }
            }
        }
        .tovar_box{
            text-align: center;
            p{
                font-size: 20px;
                font-weight: 600;
            }
        }
        .tovar_list{
            margin: 80px auto 0;
            max-width: 100%;
            position: relative;
            .owl-item{
                margin: auto auto 10px ;
            }
            .tovar_box{
                width: auto;
                padding: 15px 20px;
                border: 2px rgba(0, 0, 0, 0.2) solid;
                border-radius: 25px;
                margin: 20px 15px;
                .trans();
                &:hover{
                    box-shadow: 0 5px 10px rgba(0, 0, 0, .3);
                    border-color: @green;
                }
                img{
                    width: 135px;
                }
                .line{
                    width: 50px;
                    height: 2px;
                    background: @green;
                    margin: 20px auto 0;
                }
                p{
                    margin: 0px auto 20px;
                    span{
                        font-family: 'Circe';
                        font-weight: 600;
                    }
                }
            }
            .owl-nav{
                z-index: 8;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-top: 50px;
                position: relative;
                .owl-next{
                    width: 73px;
                    height: 73px;
                    z-index: 5;
                    background: url(../img/rightgreen.png) 50% 50% / cover no-repeat;
                    margin-left: 65px;
                }
                .owl-prev{
                    width: 73px;
                    height: 73px;
                    background: url(../img/leftgreen.png) 50% 50% / cover no-repeat;
                    margin-right: 65px;
                }
            }
            .owl-dots{
                height: 0;
                bottom: 46px;
                position: relative;
                display: flex;
                justify-content: center;
                z-index: 888;
                margin-bottom: 50px;
                .owl-dot{
                    width: 20px;
                    height: 20px;
                    background: rgba(0, 0, 0, .3);
                    border-radius: 50%;
                    margin: 0 10px;
                    .trans();
                    &:first-child{
                        margin-left: 0;
                    }
                    &:last-child{
                        margin-right: 0;
                    }
                    &.active{
                        background: @green;
                    }
                }
            }
        }
    }
}
.whatsapp{
    width: 100%;
    background: rgb(247, 246, 242);
    position: relative;
    z-index: 0;
    &::after{
        content: '';
        background: url(../img/background_01.png) 50% 50% / contain no-repeat;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 180px;
        height: 169px;
    }
    &::before{
        content: '';
        background: url(../img/background_02.png) 50% 50% / contain no-repeat;
        position: absolute;
        right: 0;
        top: 0;
        width: 152px;
        height: 186px;
    }
    .wrap{
        padding: 60px 5px;
        height: 100%;
        .flex(center, center);
        .text_whatsapp{
            margin-right: 50px;
            z-index: 2;
            p{
                font-size: 20px;
            }
            h4{
                font-family: Arial, Helvetica, sans-serif;
                font-size: 30px;
                span{
                    .span();
                    font-family: Arial;
                }
            }
        }
        .form_whatsapp{
            z-index: 2;
            form{
                display: flex;
                flex-direction: column;
                align-items: center;
                input[type="number"]{
                    background: #fff;
                    width: 220px;
                    text-align: start;
                    font-weight: 500;
                    padding: 15px 10px;
                    font-size: 16px;
                    border: 2px rgb(190, 190, 190) solid;
                    border-radius: 10px;
                    font-family: 'Circe';
                    .trans();
                    -webkit-appearance: none;
                    margin-bottom: 25px;
                    &::placeholder{
                        text-align: center;
                    }
                    &:focus{
                        border-color: @green;
                        &::placeholder{
                            .trans();
                            opacity: 0;
                        }
                    }
                }
                input[type="submit"]{
                    width: 100%;
                    font-family: 'Circe';                    
                    background: @green;
                    color: #fff;
                    font-weight: 500;
                    padding: 15px 0;
                    font-size: 18px;
                    border: none;
                    border-radius: 10px;
                    cursor: pointer;
                    -webkit-appearance: none;
                    .trans();
                    &:hover{
                        background: @dark_green;
                    }
                }
            }
        }
    }
}
