.gallery{
    width: 100%;
    background: url(../img/gallery/box-price.png) 0% 100% / cover no-repeat;
    height: 800px;
    .wrap{
        position: relative;
        .container_gallery_desktop{
            .owl-nav{
                display: flex;
                text-align: center;
                justify-content: center;
                align-items: center;
                .owl-next{
                    background: url(../img/gallery/play-button.svg) 50% 50% / cover no-repeat;
                    width: 73px;
                    height: 73px;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: -80px;
                    margin-top: -25px;
                }
                .owl-prev{
                    background: url(../img/gallery/play-button.svg) 50% 50% / cover no-repeat;
                    width: 73px;
                    height: 73px;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%) rotate(180deg);
                    left: -80px;
                    margin-top: -25px;
                }
            }
            .owl-dots{
                height: auto;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-top: 25px;
                position: relative; 
                .owl-dot{
                    width: 16px;
                    height: 16px;
                    background: rgba(0, 0, 0, .3);
                    border-radius: 50%;
                    margin: 0 10px;
                    .trans();
                    &:last-child{
                       margin-right: 0; 
                    }
                    &:first-child{
                        margin-left: 0;
                    }
                    &.active{
                        background: @green;
                    }
                }
            }
        }
        .container_gallery{
            padding: 100px 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            .box_gallery{
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                justify-content: center;
                .size{
                    width: 360px;
                    height: 225px;
                    margin: 5px;
                    position: relative;
                    border-radius: 5px; 
                    overflow: hidden;
                    a{
                        &::after{
                            content: '';
                            width: 70px;
                            height: 70px;
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            margin-top: -35px;
                            margin-left: -35px;
                            .trans();
                            transition-delay: .1s;
                            background: url(../img/gallery/zoom-tool.png) 50% 50% / cover no-repeat;
                            opacity: 0;
                        }
                        &::before{
                            content: '';
                            width: 100%;
                            height: 100%;
                            position: absolute;
                            top: 0;
                            left: 0;
                            background: rgba(0, 0, 0, .6);
                            opacity: 0;
                            .trans();
                        }
                        &:hover{
                            &::after,&::before{
                                opacity: 1;
                            }
                        }
                        width: 100%;
                        height: 100%;
                        z-index: 8;
                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            object-position: center;
                        }
                    }
                }
            }
        }
        >img{
            width: 150px;
            position: absolute;
            bottom: 60px;
            right: 20px;
            transform: rotate(10deg);
        }
    }
}
